.skills-main-div {
  display: flex;
}

.dark-mode {
  color: white !important;
}

.skills-text-div {
  margin-left: 50px;
}

.skills-text {
  font-size: 18px;
}

.skills-main-div > * {
  flex: 1;
  margin-bottom: 30px;
}

.skills-image-div > img {
  max-width: 100%;
  display: block;
  margin: auto;
  height: auto;
}

.skills-heading {
  font-size: 42px;
  font-weight: 400;
}

/* Media Query */
@media (max-width: 1380px) {
  .skills-heading {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  .skills-heading {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
  }
  .skills-main-div {
    flex-direction: column;
  }
  .skills-text-div {
    margin-left: 0px;
    margin: 20px;
  }
  .skills-text {
    font-size: 18px;
  }
  .skills-text-subtitle {
    font-size: 18px;
    text-align: center;
  }
  .skills-image-div {
    /* display: none; */
    order: 2;
  }
  .dark-mode {
    margin-top: 0rem;
  }
}
